@media only screen and (max-width: 760px),
(min-device-width: 768px) and (max-device-width: 1024px) {
    table, thead, tbody, th, td, tr {
        display: block;
        width: 100%;
    }

    thead tr {
        position: absolute;
        top: -9999px;
        left: -9999px;

    }

    tr {
        border: 1px solid #ccc;

    }

    td {
        border: none;
        border-bottom: 1px solid #eee;
        position: relative;
        padding-left: 50%;
        width: 100% !important;
    }

    td:before {
        position: absolute;
        top: 6px;
        left: 6px;
        width: 45%;
        padding-right: 10px;
        white-space: nowrap;
    }

    td:nth-of-type(1):before {
        content: "Check";
    }

    td:nth-of-type(2):before {
        content: "Added date";
    }

    td:nth-of-type(3):before {
        content: "Title";
    }

    td:nth-of-type(4):before {
        content: "Description";
    }

    td:nth-of-type(5):before {
        content: "Priority";
    }

    td:nth-of-type(6):before {
        content: "Start date";
    }

    td:nth-of-type(7):before {
        content: "Deadline";
    }

    td:nth-of-type(8):before {
        content: "Delete";
    }
}



